import React, { useEffect, useRef, useState } from "react";
import * as joint from "jointjs";
import "jointjs/dist/joint.css";
import "jointjs/dist/joint.shapes.uml.js";

export default function App() {
  const graphRef = useRef(null);
  const paperRef = useRef(null);

  const [diagramJSON, setDiagramJSON] = useState("");
  const [selectedElement, setSelectedElement] = useState(null);
  const [editName, setEditName] = useState("");
  const [attributes, setAttributes] = useState([
    { name: "", type: "String", isList: false },
  ]);

  const getAllTypes = () => {
    const primitiveTypes = ["String", "Integer", "Boolean", "Float", "Double"];
    const classTypes = graphRef.current
      ? graphRef.current
          .getElements()
          .map((el) => el.attr(".uml-class-name-text/text"))
          .filter((name) => !!name)
      : [];

    return [...primitiveTypes, ...new Set(classTypes)];
  };

  useEffect(() => {
    graphRef.current = new joint.dia.Graph();

    paperRef.current = new joint.dia.Paper({
      el: document.getElementById("paper"),
      model: graphRef.current,
      width: 900,
      height: 600,
      gridSize: 10,
      drawGrid: true,
      background: { color: "#f9f9f9" },
      interactive: { linkMove: false },
    });

    paperRef.current.on("element:pointerclick", (elementView) => {
      const element = elementView.model;
      setSelectedElement(element);

      setEditName(element.attr(".uml-class-name-text/text") || "");
      const savedAttrs = element.get("rawAttributes");
      if (savedAttrs && savedAttrs.length > 0) {
        setAttributes(savedAttrs);
      } else {
        // fallback parsing old format
        const rawAttrs = element.get("attributes") || [];
        const parsedAttrs = rawAttrs.map((line) => {
          const [name, typeRaw] = line
            .replace("+", "")
            .split(":")
            .map((x) => x.trim());
          const isList = /^List\[.+\]$/.test(typeRaw);
          const type = isList ? typeRaw.match(/^List\[(.+)\]$/)[1] : typeRaw;
          return { name, type, isList };
        });
        setAttributes(parsedAttrs);
      }
    });

    paperRef.current.on("blank:pointerdown", () => {
      setSelectedElement(null);
    });
  }, []);

  const updateElement = () => {
    if (!selectedElement) return;

    selectedElement.attr(".uml-class-name-text/text", editName);
    selectedElement.set("name", editName);

    const attrStrings = attributes
      .filter((attr) => attr.name)
      .map((attr) => {
        const typeText = attr.isList ? `List[${attr.type}]` : attr.type;
        const fullLine = `+ ${attr.name}: ${typeText}`;
        return breakLongLine(fullLine, 20);
      });

    selectedElement.attr(".uml-class-attrs-text/text", attrStrings.join("\n"));
    selectedElement.set("attributes", attrStrings);
    selectedElement.set("rawAttributes", attributes);
    const lineCount = `${editName}\n${attrStrings.join("\n")}`.split(
      "\n"
    ).length;
    const height = 20 + lineCount * 20 + 20;
    selectedElement.resize(200, height);

    createAssociationLinks(selectedElement);
  };

  function breakLongLine(str, maxLength) {
    if (str.length <= maxLength) return str;
    const parts = [];
    for (let i = 0; i < str.length; i += maxLength) {
      parts.push(str.slice(i, i + maxLength));
    }
    return parts.join("\n");
  }

  const addClass = () => {
    const graph = graphRef.current;
    const umlClass = new joint.shapes.uml.Class({
      position: { x: 50 + Math.random() * 400, y: 50 + Math.random() * 400 },
      size: { width: 200, height: 100 },
      attrs: {
        ".uml-class-name-rect": { fill: "#6a9fb5" },
        ".uml-class-attrs-rect": { fill: "#97b0c9" },
        ".uml-class-name-text": {
          "font-family": "monospace",
          "font-weight": "bold",
          "font-size": 16,
          "white-space": "pre-wrap",
          "word-break": "break-word", // NEW
          "overflow-wrap": "break-word", // NEW
        },
        ".uml-class-attrs-text": {
          "font-family": "monospace",
          "font-size": 14,
          "white-space": "pre-wrap", // changed from "pre"
          "word-break": "break-word", // NEW
          "overflow-wrap": "break-word", // NEW
        },
      },
      name: "NewClass",
      attributes: ["+ attr1: String"],
    });
    graph.addCell(umlClass);
  };

  const addInheritance = () => {
    const graph = graphRef.current;

    const elements = graph
      .getElements()
      .filter((e) => e.isElement() && e instanceof joint.shapes.uml.Class);

    if (elements.length < 2) {
      alert("Add at least two classes first!");
      return;
    }

    const source = elements[elements.length - 2];
    const target = elements[elements.length - 1];

    const link = new joint.dia.Link({
      source: { id: source.id },
      target: { id: target.id },
      attrs: {
        ".connection": { stroke: "black", "stroke-width": 2 },
        ".marker-target": { fill: "black", d: "M 10 0 L 0 5 L 10 10 z" },
      },
      router: { name: "orthogonal" },
      connector: { name: "rounded" },
    });

    graph.addCell(link);
  };
  const deleteSelectedClass = () => {
    if (!selectedElement) {
      alert("No class selected to delete!");
      return;
    }
    graphRef.current.removeCells([selectedElement]);
    setSelectedElement(null);
    setEditName("");
    setAttributes([{ name: "", type: "String" }]);
  };

  const exportJSON = () => {
    const json = JSON.stringify(graphRef.current.toJSON(), null, 2);
    setDiagramJSON(json);
  };
  const saveToAPI = async () => {
    const json = graphRef.current.toJSON();
    console.log("Saving JSON:", json); // Debugging line to check JSON before sending
    try {
      const response = await fetch("http://127.0.0.1:5000/save-diagram", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(json),
      });

      if (!response.ok) throw new Error("Server error");

      const result = await response.json();
      console.log("Saved:", result.message);
    } catch (err) {
      console.error("Save failed:", err);
    }
  };

  // const importJSON = () => {
  //   if (!diagramJSON) return;
  //   try {
  //     graphRef.current.fromJSON(JSON.parse(diagramJSON));
  //   } catch {
  //     alert("Invalid JSON");
  //   }
  // };
  const createAssociationLinks = (sourceElement) => {
    const graph = graphRef.current;
    const allElements = graph.getElements();

    // Get class names to element map
    const nameToElement = {};
    allElements.forEach((el) => {
      const name = el.attr(".uml-class-name-text/text");
      if (name) nameToElement[name] = el;
    });

    const attributes = sourceElement.get("attributes") || [];
    attributes.forEach((attrLine) => {
      const [name, typeRaw] = attrLine
        .replace("+", "")
        .split(":")
        .map((s) => s.trim());
      if (!typeRaw) return;

      // Extract type name even if wrapped (e.g., List[MenuItem], Optional[MenuItem])
      const match = typeRaw.match(/List\s*\[\s*([^\]]+?)\s*\]|Optional\s*\[\s*([^\]]+?)\s*\]|(\w+)/);
      if (!match) return;
      const typeName = match[1] || match[2] || match[3];

      const targetElement = nameToElement[typeName];
      if (targetElement && targetElement.id !== sourceElement.id) {
        // Avoid duplicate links
        const existingLink = graph.getLinks().find((link) => {
          return (
            link.get("source").id === sourceElement.id &&
            link.get("target").id === targetElement.id
          );
        });
        if (!existingLink) {
          const link = new joint.dia.Link({
            source: { id: sourceElement.id },
            target: { id: targetElement.id },
            attrs: {
              ".connection": { stroke: "#888", "stroke-dasharray": "5,2" },
              ".marker-target": { d: "M 4 0 L 0 2 L 4 4 z", fill: "#888" },
            },
            router: { name: "orthogonal" },
            connector: { name: "rounded" },
          });
          graph.addCell(link);
        }
      }
    });
  };

  const handleAttrChange = (index, field, value) => {
    const updated = [...attributes];
    updated[index][field] = value;
    setAttributes(updated);
  };

  const addAttributeField = () => {
    setAttributes([...attributes, { name: "", type: "String", isList: false }]);
  };

  return (
    <div
      style={{ display: "flex", flexDirection: "column", alignItems: "center" }}
    >
      <h1>UI: Prototype 01</h1>
      <div style={{ marginBottom: 10 }}>
        <button onClick={addClass} style={{ marginRight: 10 }}>
          Add Class
        </button>
        <button
          onClick={deleteSelectedClass}
          style={{
            marginRight: 10,
            backgroundColor: "#e74c3c",
            color: "white",
          }}
        >
          Delete Class
        </button>

        <button onClick={addInheritance} style={{ marginRight: 10 }}>
          Add Inheritance
        </button>
        {/* <button onClick={exportJSON} style={{ marginRight: 10 }}>
          Export JSON
        </button> */}
        <button onClick={saveToAPI} style={{ marginRight: 10 }}>
          Save to API
        </button>
      </div>

      {selectedElement && (
        <div
          style={{
            marginBottom: 20,
            border: "1px solid #ccc",
            padding: 10,
            width: 300,
            backgroundColor: "#f5f5f5",
          }}
        >
          <h3>Edit Class</h3>
          <label>
            Name:
            <br />
            <input
              style={{ width: "100%", marginBottom: 10 }}
              value={editName}
              onChange={(e) => setEditName(e.target.value)}
            />
          </label>

          <label>Attributes:</label>
          {attributes.map((attr, i) => (
            <div
              key={i}
              style={{ display: "flex", marginBottom: 5, alignItems: "center" }}
            >
              <input
                placeholder="name"
                value={attr.name}
                onChange={(e) => handleAttrChange(i, "name", e.target.value)}
                style={{ flex: 1, marginRight: 5 }}
              />
              <select
                value={attr.type}
                onChange={(e) => handleAttrChange(i, "type", e.target.value)}
              >
                {getAllTypes().map((type) => (
                  <option key={type} value={type}>
                    {type}
                  </option>
                ))}
              </select>
              <div
                style={{ marginLeft: 8, display: "flex", alignItems: "center" }}
              >
                <span style={{ fontSize: 12, marginRight: 5 }}>List</span>
                <label
                  style={{
                    position: "relative",
                    display: "inline-block",
                    width: 34,
                    height: 18,
                  }}
                >
                  <input
                    type="checkbox"
                    checked={attr.isList}
                    onChange={(e) =>
                      handleAttrChange(i, "isList", e.target.checked)
                    }
                    style={{ opacity: 0, width: 0, height: 0 }}
                  />
                  <span
                    style={{
                      position: "absolute",
                      cursor: "pointer",
                      top: 0,
                      left: 0,
                      right: 0,
                      bottom: 0,
                      backgroundColor: attr.isList ? "#4caf50" : "#ccc",
                      transition: ".4s",
                      borderRadius: "34px",
                    }}
                  >
                    <span
                      style={{
                        position: "absolute",
                        height: "14px",
                        width: "14px",
                        left: attr.isList ? "18px" : "2px",
                        bottom: "2px",
                        backgroundColor: "white",
                        transition: ".4s",
                        borderRadius: "50%",
                      }}
                    />
                  </span>
                </label>
              </div>
            </div>
          ))}

          <button onClick={addAttributeField} style={{ marginBottom: 10 }}>
            + Add Attribute
          </button>

          <button onClick={updateElement}>Save Changes</button>
        </div>
      )}

      <div id="paper" style={{ border: "1px solid #ccc" }}></div>

      <textarea
        placeholder="Diagram JSON here for import/export"
        style={{
          width: 900,
          height: 200,
          marginTop: 20,
          fontFamily: "monospace",
        }}
        value={diagramJSON}
        onChange={(e) => setDiagramJSON(e.target.value)}
      />
      <p style={{ maxWidth: 900, fontSize: 12, color: "#666" }}>
        Click a class to select and edit attributes.
      </p>
    </div>
  );
}
